import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
  	isUser: JSON.parse(localStorage.getItem('logged')) || false,
    message: 'Сайт знаходиться на стадії розробки. Якщо Ви зауважите певні проблеми у роботі з сайтом, звертайтесь до власника сайту (контакти розміщені нижче)',
  },
  mutations: {
    isLogIn(state) {
      state.isUser = true;
      localStorage.setItem('logged', JSON.stringify(state.isUser));
    },
    isLogOut(state) {
      state.isUser = false;
      localStorage.setItem('logged', JSON.stringify(state.isUser));
    },
   
  },
  actions: {},
  getters: {
  	User: state => {
      return state.isUser;
    },
    Message: state => {
      return state.message;
    },
  }
});
