        <div class="container-fluid">
            <!-- <input type="color"> -->
            <transition name='slide-fade'>
                <div v-if='showMessage' class="message">
                    {{message}}
                    <span class="close" @click='showMessage = !showMessage'>&times;</span>
                </div>
            </transition>
            
            <div class="row">
                <div class="col-12 col-xl-3" :class='{"d-none": adminRoute}'>
                    <profile />
                </div>
                <div class="col-12 col-xl-9" :class='{"col-md-12": adminRoute, "col-xl-12": adminRoute,}'>
