<script>
    const Profile = () => import('./views/Profile');
    const Menu = () => import('./components/Menu');
    const PageBottom = () => import('./views/PageBottom');

    
    export default {
        data() {
            return {
                showMessage: true,
            }
        },
        computed: {
            message() {
                return this.$store.getters.Message;
            },
            adminRoute() {
                return this.$route.fullPath.includes('admin',0);
            }
        },
        components: {
            Profile,
            Menu,
            PageBottom,        
        }
    }
</script>
