import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import VueAnalytics from 'vue-analytics';
import cyrillicToTranslit from 'cyrillic-to-translit-js';
import VueMoment from 'vue-moment';
import VueDisqus from 'vue-disqus';

import VueFire from 'vuefire';

import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';

Vue.config.productionTip = false;

Vue.prototype.$_friendlyUrl = function (value) {
    return cyrillicToTranslit({ preset: "uk" }).transform(value.toLowerCase(), "_")
};
Vue.use(VueFire);
Vue.use(VueMoment);
Vue.use(VueAnalytics, {
  id: 'UA-136388096-1',
  router
});

Vue.use(VueDisqus);

firebase.initializeApp({
    apiKey: "AIzaSyAbHEpHDaTm0ed3czwebEbQs1cwNcwlpn0",
    authDomain: "portfolio-11879.firebaseapp.com",
    databaseURL: "https://portfolio-11879.firebaseio.com",
    projectId: "portfolio-11879",
    storageBucket: "portfolio-11879.appspot.com",
    messagingSenderId: "165637758076"
});

firebase.auth().onAuthStateChanged(() => {
	let app = '';
  	if (!app) {
    	const app = new Vue({
        	router,
        	store,
        	render: h => h(App),
      	}).$mount("#app");
    }
});

Vue.prototype.$_db = firebase.firestore();
Vue.prototype.$_auth = firebase.auth();