import firebase from 'firebase/app';
import 'firebase/auth';

import Vue from "vue";
import Router from "vue-router";
import Meta from 'vue-meta';

Vue.use(Router);
Vue.use(Meta);

const protect = (to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if (requiresAuth && !currentUser) next('login');
  else next();
};

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: () => import('./views/Home.vue'),      
    },
    {
      path: "/portfolio",
      component: () => import("./views/Portfolio.vue"),
      children: [
        {
          path: "",
          name: "portfolioWorks",
          component: () => import("./components/portfolio/PortfolioWorks"),
        },
        {
          path: ":title",
          name: "single-work",
          component: () => import("./components/portfolio/PortfolioSingle.vue"),
          props: true
        },
      ],
    },
    
    {
      path: "/blog",
      component: () => import("./views/Blog.vue"),
      children: [
        {
          path: "",
          name: "blogArticles",
          component: () => import("./components/blog/BlogArticles.vue"),
        },
        {
          path: ":title",
          name: "single-article",
          component: () => import("./components/blog/BlogSingle.vue"),
          props: true
        },
      ]
    },
    
    {
      path: "/admin",
      name: "admin",
      redirect: '/admin/personal',
      component: () => import("./views/Admin.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect,
      children: [
        {
          path: "blog",
          name: "manage_blog",
          component: () => import("@/components/admin/manage/ManageBlog.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect
        },
        {
          path: "personal",
          name: "manage_personal",
          component: () => import("@/components/admin/manage/ManagePersonal.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect
        },
        {
          path: "portfolio",
          name: "manage_works",
          component: () => import("@/components/admin/manage/ManageWorks.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect
        },
        {
          path: "drafts",
          name: "manage_drafts",
          component: () => import("@/components/admin/manage/ManageDrafts.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect
        },
        {
          path: "personal/new_info",
          name: "new_info",
          component: () => import("./components/admin/manage/support/addInfo.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect,
        },
        {
          path: "blog/new_post",
          name: "new_post",
          component: () => import("./components/blog/NewPost.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect
        },
        {
          path: "blog/edit_post/:title",
          name: "edit_post",
          component: () => import("./components/blog/EditPost.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect,
        },
        {
          path: "portfolio/new_work",
          name: "new_work",
          component: () => import("./components/portfolio/NewWork.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect
        },
        {
          path: "portfolio/edit_work/:title",
          name: "edit_work",
          component: () => import("./components/portfolio/EditWork.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect
        },
        {
          path: "drafts/edit_draft/:title",
          name: "edit_draft",
          component: () => import("./components/blog/EditDraft.vue"),
          meta: {
            requiresAuth: true
          },
          beforeEnter: protect
        },
      ],
    },

    {
      path: "/live_preview",
      name: "live_preview",
      component: () => import("./components/blog/PostPreview.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect,
      props: true
    },

    {
      path: "/work_preview",
      name: "work_preview",
      component: () => import("./components/portfolio/WorkPreview.vue"),
      meta: {
        requiresAuth: true
      },
      beforeEnter: protect
    },

    {
      path: "/login",
      name: "login",
      component: () => import("./components/auth/Login.vue"),
    },
    {
      path: '*',
      name: 'empty',
      component: () => import("./views/NotFound.vue")
    }
  ]
});
